import {
  EntityItem,
  FilterItem,
  KeywordItem,
  LanguageItem,
  MacroSearchItem,
  SourceItem,
  TopicItem,
} from "@rp/react/tanstack-api/cqs-query-context"

const isEntityItem = (item: FilterItem): item is EntityItem => item.type === "entity"
const isSourceItem = (item: FilterItem): item is SourceItem => item.type === "source"
const isTopicItem = (item: FilterItem): item is TopicItem => item.type === "rp_topic"
const isLanguageItem = (item: FilterItem): item is LanguageItem => item.type === "language"
const isKeywordItem = (item: FilterItem): item is KeywordItem => item.type === "keyword"
const isMacroSearchItem = (item: FilterItem): item is MacroSearchItem =>
  item.type.substring(0, 6) === "macro_"

export { isEntityItem, isSourceItem, isTopicItem, isLanguageItem, isKeywordItem, isMacroSearchItem }
